import React, { useState, ChangeEvent } from 'react';
import * as curlconverter from 'curlconverter';
import './CurlConverterWidget.css'; // Assurez-vous que le chemin est correct

const CurlConverterWidget: React.FC = () => {
  const [curlCommand, setCurlCommand] = useState('');
  const [response, setResponse] = useState('');

  const handleSendRequest = async () => {
    try {
      const fetchCode = curlconverter.toNodeFetch(curlCommand);
      // Il est recommandé de trouver une alternative à eval pour des raisons de sécurité.
      // Par exemple, vous pouvez utiliser une nouvelle Function ou une bibliothèque d'exécution sécurisée.
      new Function(fetchCode)();
    } catch (error) {
      console.error('Erreur de conversion ou d\'exécution de la commande cURL :', error);
      setResponse('Erreur de conversion ou d\'exécution de la commande. Veuillez vérifier la syntaxe.');
    }
  };

  return (
    <div className="container">
      <textarea
        className="textarea"
        placeholder="Collez votre commande cURL ici"
        rows={5}
        value={curlCommand}
        onChange={(e: ChangeEvent<HTMLTextAreaElement>) => setCurlCommand(e.target.value)}
      />
      <button
        className="button"
        onClick={handleSendRequest}
      >
        Envoyer la requête
      </button>
      <pre className="pre">{response}</pre>
    </div>
  );
};

export default CurlConverterWidget;
